<template>
    <el-form ref="request-form" :model="form" label-width="80px">
        <el-form-item label="请求URL">
            <el-input v-model="form.url" type="textfield" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="请求JSON">
            <el-input
        type="textarea" autosize
        v-model="form.payload"
        :disabled="true"
      ></el-input>
        </el-form-item>
        <el-form-item label="返回json">
            <el-input
        type="textarea" autosize
        v-model="form.response"
        :disabled="true"
      ></el-input>
        </el-form-item>
        <el-button type="primary" plain  v-on:click="onSubmit()">调用</el-button>
    </el-form>
</template>

<script>
var localRequestConfig = {};

export
default {
    name: "RequestPanel",
    components: {},
    data() {
        return {
            form: {
                url: "",
                payload: "",
                response: ""
            },
        };
    },
    mounted() {
        const vm = this;
        this.$root.$on("access-token", function (data) {
            console.log(data);
        });
        this.$root.$on("table-data", function (data) {
            vm.form.url = data.path;
            vm.form.payload = this.$qs.stringify(data.requestModel);
            localRequestConfig.method = data.httpRequestMethod;
            // todo
            //签名：https://www.npmjs.com/package/crypto-js
            localRequestConfig.url = data.path;
            var name = 'requestModel';
            if (data.httpRequestMethod == 'get')
                localRequestConfig.params = data[name];
            if (data.httpRequestMethod == 'post')
                localRequestConfig.data = data.requestModel;
        });
    },
    methods: {
        onSubmit() {
            console.log(localRequestConfig);
            this.$axios.request(localRequestConfig).then(response => (this.form.response = [JSON.stringify(response.data)])).
            catch(function (error) { // 请求失败处理
                console.log(error);
            });

        }
    }
};

</script>
